import "./styles.css";
const dog1 = new Audio("../sound/mark.mp3");

window.onload = function () {
  dog1.play();
};
// Link to sound files
const dog = new Audio("../sound/mark.mp3");
const cougar = new Audio("../sound/cougar.mp3");
const lion = new Audio("../sound/lion.mp3");

// Select DOM elements
const animals = document.querySelectorAll(".animal");

// Play animal sound
function makeSound(name) {
  switch (name) {
    case "lion":
      lion.play();
      break;
    case "cougar":
      cougar.play();
      break;
    case "dog":
      dog.play();
      break;
    default:
      return;
  }
}

// Add event listeners
for (let i = 0; i < animals.length; i++) {
  animals[i].addEventListener("click", function () {
    let animal = this.innerText.toLowerCase();
    makeSound(animal);
  });
}
